/* Centrado total de la sección */
.ceremonyfiesta-section {
  background: transparent !important; /* deja ver el fondo aplicado a toda la sección */
  border-radius: 16px;
  box-shadow: none; /* sin sombra para que el fondo sea protagonista */
  border: none;
  margin: 0 auto; /* centered inside flex container */
  transform: translateY(-10px); /* move slightly up visually */
  padding: 2rem 1.5rem;
  max-width: 700px;
  min-height: unset;
  width: min(700px, 92vw);
  display: block;
  box-sizing: border-box;
  position: relative;
  overflow-x: visible;
}

.ceremonyfiesta-content-centered {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
    max-width: 500px;
    width: 100%;
  box-sizing: border-box;
}
.ceremonyfiesta-img {
  width: 200px;
  height: 200px;
  object-fit: cover;
  border-radius: 50%;
  border: 4px solid var(--border);
  box-shadow: 0 2px 16px rgba(191,169,128,0.10);
  display: block;
  margin: 0 auto 1.2rem auto;
  background: #fff;
}

.ceremonyfiesta-img-round {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  object-fit: cover;
  display: block;
  margin: 0 auto 1.2rem auto;
}
.ceremonyfiesta-icon {
  margin-bottom: 0.5rem;
}
.ceremonyfiesta-title {
  font-family: 'Dantina', sans-serif;
  font-size: 2.2rem;
  color: var(--text);
  text-align: center;
  margin: 0.5rem 0 1.2rem 0;
  font-weight: 400;
  letter-spacing: 0.04em;
}
/* Eliminado duplicado ceremonyfiesta-img */
.ceremonyfiesta-label {
  font-size: 0.9rem;
  color: var(--text-muted);
  letter-spacing: 0.1em;
  margin-top: 1.2rem;
  margin-bottom: 0.2rem;
  text-transform: uppercase;
  font-family: 'Playfair Display', serif;
  text-align: center;
}
.ceremonyfiesta-date, .ceremonyfiesta-place {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.ceremonyfiesta-place {
  font-size: 1rem;
  color: var(--text-muted);
  margin-bottom: 0.5rem;
  text-align: center;
}
.ceremonyfiesta-buttons {
  display: flex;
  justify-content: center;
  gap: 0.75rem;
  margin-top: 1.2rem;
  flex-wrap: nowrap; /* keep in a single line on desktop/tablet */
}
.ceremonyfiesta-btn {
  background: #fff;
  border: 1.5px solid var(--primary);
  color: var(--primary);
  border-radius: 28px;
  padding: 0.48rem 0.9rem; /* smaller button */
  min-height: 38px; /* still tap-friendly */
  font-size: 0.9rem;
  letter-spacing: 0.02em;
  text-transform: none;
  font-family: 'Roboto', sans-serif;
  cursor: pointer;
  text-decoration: none;
  transition: background 0.2s, color 0.2s, border-color 0.2s, box-shadow 0.2s;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  text-align: center;
  flex: 0 0 auto; /* don't grow, keep compact */
  max-width: none;
}
.ceremonyfiesta-btn:hover {
  background: var(--primary);
  color: #fff;
  box-shadow: 0 4px 20px rgba(0,0,0,0.08);
}

/* Fondo de toda la sección de ceremonia (envolvente) */
.ceremonyfiesta-bg {
  width: 100%;
  /* Full viewport like the hero */
  min-height: 100vh;
  min-height: 100svh; /* modern mobile viewport units */
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0; /* avoid extra height beyond viewport */
  background-color: transparent;
  background-attachment: scroll; /* safer on mobile */
  position: relative;
  scroll-snap-align: center; /* Keep this line for mobile */
  scroll-snap-stop: normal; /* Keep this line for mobile */
}

@media (min-width: 769px) {
  .ceremonyfiesta-bg {
    scroll-snap-align: unset;
    scroll-snap-stop: normal;
  }
}

.ceremonyfiesta-bg::before {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  top: -1px; /* overlap slightly to avoid gaps */
  height: 160px; /* top wave height */
  background: url('../assets/wave-mid-brush.png') no-repeat 50% 0;
  background-size: 100% 100%; /* fill width and height */
  transform: rotate(180deg); /* rotate 180° vertically */
  pointer-events: none;
  z-index: 0;
}

.ceremonyfiesta-bg::after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  bottom: -1px; /* overlap slightly to avoid gaps */
  height: 160px; /* taller wave */
  background: url('../assets/wave-mid-brush.png') no-repeat 50% 100%;
  background-size: 100% 100%; /* fill width and height */
  pointer-events: none;
  z-index: 0;
}

@media (max-width: 768px) {
  .ceremonyfiesta-bg {
  min-height: 100vh;
  min-height: 100svh;
  padding: 0;
  }
  .ceremonyfiesta-bg::before { height: 120px; }
  .ceremonyfiesta-bg::after { height: 120px; }
}

@media (max-width: 480px) {
  .ceremonyfiesta-bg {
  min-height: 100vh;
  min-height: 100svh;
  padding: 0;
  }
  .ceremonyfiesta-bg::before { height: 96px; }
  .ceremonyfiesta-bg::after { height: 96px; }
}

/* Ensure the inner card is above the wave */
.ceremonyfiesta-section {
  position: relative;
  z-index: 1;
}

/* Responsive */
@media (max-width: 768px) {
  .ceremonyfiesta-img,
  .ceremonyfiesta-img-round {
    width: 160px;
    height: 160px;
  }
}

@media (max-width: 480px) {
  .ceremonyfiesta-img,
  .ceremonyfiesta-img-round {
    width: 140px;
    height: 140px;
  }
  .ceremonyfiesta-buttons {
  flex-direction: column;
  align-items: stretch;
  gap: 0.6rem; /* stack on mobile */
  }
  .ceremonyfiesta-btn {
  width: 100%;
  justify-content: center;
  flex: 0 0 auto; /* evita que crezca en columna y se haga gigante */
  padding: 0.55rem 1rem; /* un poco menos de alto en mobile */
  }
}
